import "./App.css";
import {Peer} from 'peerjs'
import { io } from "socket.io-client";
import { useEffect, useRef, useState } from "react";
function App() {
  const constraint = {
    video: true,
    audio:true
  }  
  const ref_mainCamera = useRef(null);
  const ref_subCamera = useRef(null);
  const ref_peer = useRef(new Peer());

  const [yourID, set_yourID]= useState('')
  const ref_iptYourID = useRef('');
  const handleClick = ()=>{
    if(navigator.mediaDevices && navigator.mediaDevices.getUserMedia)
    {
      navigator.mediaDevices.getUserMedia(constraint).then(stream=>{
        ref_subCamera.current.srcObject=stream;

        var call = ref_peer.current.call(ref_iptYourID.current.value, stream);
        call.on('stream',remoteStream=>{
          ref_mainCamera.current.srcObject = remoteStream;
        })
      })

    }
  }
  useEffect(()=>{
    const socket = new io('http://localhost:80',{ transports : ['websocket'] });
    var conn = ref_peer.current.connect('another-peers-id');
    ref_peer.current.on('open',(id)=>{
      set_yourID(id)
      
    })
    ref_peer.current.on('call',call=>{
      navigator.mediaDevices.getUserMedia(constraint).then(stream=>{
        call.answer(stream)
        ref_subCamera.current.srcObject=stream;
        call.on('stream',remoteStream=>{
          ref_mainCamera.current.srcObject = remoteStream;
        })
      })
    })
  },[])
  return (
    <div className="container-callVideo">
      <div className="main-callVideo">
        <div className="body-callVideo">
          <div className="header-callVideo">
              {`Your ID : ${yourID}`}
          </div>
          <div className="content-callVideo">
            <div className="body-mainCamera">
              <video ref={ref_mainCamera}
                src="/videos/Canada Nova Scotia rusted chain links on ground pan_preview.mp4"
                autoPlay={true}
                loop={true}
              ></video>
            </div>
            <div className="list-subCamera">
          <div className="body-subCamera">
          <video 
          ref={ref_subCamera}
            src="/videos/Canada Nova Scotia rusted chain links on ground pan_preview.mp4"
            autoPlay={true}
            loop={true}
          ></video>
        </div>
          </div>
          </div>
          <div className="footer-callVideo">
            <input id="iptFriendID"type={'text'} ref={ref_iptYourID}/>
            <button id="btnFriendID" onClick={()=>{
              handleClick();
            }}>Send</button>
          </div>
          <div className="sideLeft-callVideo"></div>
          <div className="sideRight-callVideo"></div>
          
        </div>

      </div>
    </div>
  );
}

export default App;
